@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap');

body {
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    font-weight: 400;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: aliceblue;
}

.main-container {
    border: 1px solid #000000;
    width: 320px;
    height: 533px;
    padding: 8px;
    background-color: white;
}

.header-btn {
    display: flex;
    justify-content: space-between;
    margin: 0 0 8px 0;
}

.popup {
    width: 273px;
    height: 319px;
    border: 1px solid #000000;
}

.icon-close {
    border: none;
    background-color: white;
    cursor: pointer;
}

.popup-header {
    display: flex;
    justify-content: space-between;
    margin: 0 0 16px;
}

.authorization-btn {
    display: flex;
    justify-content: space-between;
    margin: 7px 0 0 0;
}

.email {
    margin-top: 55px;
}

.enter-btn {
    margin: 7px 0 0 0;
}

.entry-field {
    width: 239px;
}

.enter-nickname {
    width: 207px;
}

.messenger {
    border: 1px solid #000000;
    width: 304px;
    height: 437px;
    padding: 8px;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
}

.message {
    background-color: #E5E5E5;
    display: inline-block;
    padding: 8px;
    margin: 4px 0;
    max-width: 257px;
}

.sent {
    float: right;
}

.time-sent {
    float: right;
    font-size: 14px;
    font-weight: 300;
}

.entry {
    display: flex;
    justify-content: space-between;
    margin: 8px 0 0 0;
}

.input-message {
    width: 267px;
    padding-left: 7px;
}